<template>
    <section id="main-content" class="main-content">
        <section class="page-action">
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('ArticleDetails') }}</span>
                        <span class="p-actions float-md-right float-left mb-wid-100">
                            <RouterLink to="/KnowledgeBase/ManageArticle" title=""
                                class="p-action-btn text-white d-md-inline-block mble_50" data-toggle-tooltip="tooltip"
                                data-original-title="Back to list"><i class="fa fa-angle-double-left"></i><br>Back to
                                list</RouterLink>
                        </span>
                    </div>
                </div>
            </div>
        </section>
        <div class="card mb-3 probx">
            <div class="card-body">
                <div class="row">
                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label :itle="ArticleData[0].TICKET_ARTICLE_TITLE"><b>{{ $t('Title') }}:</b>
                                {{ ArticleData[0].TICKET_ARTICLE_TITLE }}</label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('Status') }}:</b>
                                <span v-bind:class="getStatusClassList(ArticleData[0].ST[0].STATUS_NAME, 'STATUS_NAME')">
                                {{ ArticleData[0].ST[0].STATUS_NAME }}
                                </span>
                            </label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('Category')
                            }}:</b> {{ ArticleData[0].ST[0].CAT[0].TICKET_ARTICLE_CATEGORY_NAME }}</label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label class="d-inline-flex flex-row w-100 flex-nowrap">
                                <b class="d-flex mr-1">{{ $t('AssignTo') }}: </b>
                                <div v-if="!divTransfer">
                                    <span id="username" class="d-flex float-left mr-2">{{ AssignedText }}</span>
                                    <a v-if="UserType == 'CA'" @click="EditAssignToUser" href="javascript:;"
                                        class="d-flex"><em class="fa fa-pencil text-success"></em></a>
                                </div>
                                <div v-if="divTransfer" class="input-group w-80">
                                    <Field name='AssignToUser' class="d-flex">
                                        <select :class="{ 'form-control': true }" v-model="AssignedId"
                                            class="form-control level">
                                            <option v-for="(pitem, index) in StatusData" :key="index"
                                                :value="pitem.USER_ID">
                                                {{ pitem.USERNAME }}</option>
                                        </select>
                                        <span class="input-group-append" v-if="UserType == 'CA'">
                                            <a href="javascript:;" class="input-group-text"
                                                @click="transferArticle(ArticleData[0].TICKET_ARTICLE_ID)" title="Transfer"
                                                style="height:36px;">
                                                <i class="fa fa-exchange text-secondary"></i>
                                            </a>
                                        </span>
                                    </Field>

                                </div>
                            </label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label :title="ArticleData[0].TICKET_ARTICLE_KEYWORDS"><b>{{ $t('ShortDescription') }}:</b> {{
                                ArticleData[0].TICKET_ARTICLE_KEYWORDS }}</label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3">
                        <div class="form-group">
                            <label><b>{{ $t('Author') }}:</b> {{ ArticleData[0].ST[0].CAT[0].USER_NAME }}</label>
                        </div>
                    </div>
                    <div class="col-md-4 col-lg-3" v-if="ArticleData[0].ATTACHMENT_NAME != null">
                        <div class="form-group">
                            <label><b>{{ $t('AttachmentName') }}:</b>
                                <a :id="ArticleData[0].TICKET_ARTICLE_ID"
                                    class="downloadAttachement downarrow enable pullleft margintop3 marginright5"
                                    :title="$t('ClickHereDownloadAttachment')" :alt="$t('DownloadFile')" href="javascript:;"
                                    :value="ArticleData[0].TICKET_ARTICLE_ID"
                                    @click="DownloadAttachment(ArticleData[0].TICKET_ARTICLE_ID)">
                                    {{ ArticleData[0].ATTACHMENT_NAME }}
                                </a>
                            </label>
                        </div>
                    </div>
                    <div class="col-md-12 col-lg-12">
                        <div class="form-group">
                            <label><b>{{ $t('Description') }}:</b><span v-html="ArticleData[0].TICKET_ARTICLE_DESC">
                                </span></label>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    data() {
        return {
            ArticleData: [],
            StatusData: [],
            UserType: null,
            divTransfer: false,
            AssignedText: '',
            AssignedId: null,
            performAction: 'TRANSFAR'
        }
    },
    created: function () {
        this.GetUsersByCompanyId();
        this.UserType = this.GetUserInfo.UserType;
        setTimeout(() => { this.displayArticleDetails(); }, 2000);
    },
    methods: {
        GetUsersByCompanyId: function () {
            var url = `moduleName=KnowledgeBase`;
            DataServices.GetUsersByCompanyId(url).then(response => {
                if (JSON.parse(response.data.result).length > 0) {
                    this.StatusData = JSON.parse(response.data.result);
                }
            })
        },
        displayArticleDetails: function () {
            var url = `articleId=${this.$route.params.id}`;
            DataServices.displayArticleDetails(url).then(response => {
                if (response.data.status == 'Success') {
                    //debugger;
                    this.ArticleData = JSON.parse(response.data.result).data;
                    var AssignedData = this.StatusData.filter(x => x.USER_ID == this.ArticleData[0].TICKET_ARTICLE_ASSIGNED_TO);
                    this.AssignedText = AssignedData[0].USERNAME;
                    this.AssignedId = AssignedData[0].USER_ID;
                }
            })
        },
        EditAssignToUser: function () {
            this.divTransfer = true;
        },
        transferArticle: function (ths) {
            var vm = this;
            if (vm.AssignedId != null) {
                var params = `categoryId=${vm.AssignedId}&articleIds=${ths}&performAction=${vm.performAction}`;
                DataServices.CopyandMoveArticles(params).then(response => {
                    if (response.data.result == '1') {
                        if (vm.performAction == "TRANSFAR") {
                            vm.ShowAlert(vm.$t('TransferSuccess', [vm.$t('Article')]), "Success", true, vm.$t('Alert'));
                        }
                        vm.displayArticleDetails();
                        vm.divTransfer = false;
                    }
                })
            } else {
                vm.ShowAlert(vm.$t('SelectCategoryFirst'), "warning", true, vm.$t('Alert'));
            }
        },
        DownloadAttachment: async function (ths) {
            var vm = this;
            var url = `articleId=${ths}`
            await DataServices.GetArticleById(url).then(function (response) {
                if (response.data.status == 'Success') {
                    var ArticleData = JSON.parse(response.data.result);
                    vm.downloadURI(ArticleData[0].ATTACHMENT_PATH, ArticleData[0].ATTACHMENT_NAME, ArticleData[0].ATTACHMENT_DESC);
                }
            })
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'UnPublish':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Published':
                    returnClass = 'px-2 bg-primary text-center text-white d-block';
                    break;
                case 'Draft':
                    returnClass = 'px-2 text-center bg-warning text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
              
                
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        downloadURI: function (uri, name, desc) {
            var vm = this;
            vm.isLoading = true;
            var param = `filePath=${uri}&fileName=${name}`;
            DataServices.DownloadFile(param).then(function (response) {
                vm.isLoading = false;
                const byteData = response.data;
                const fileURL = `data:${((JSON.parse(desc)).ContentType)};base64:${byteData}`;
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', name);
                document.body.appendChild(fileLink);
                fileLink.click();
            });
            vm.isLoading = false;
        }
    }
}

</script>